<template>
  <div id="app">
    <div class="app-top">
        获取格式化时间的测试场<br/>
    </div>
    <hr/>
    <div class="bodys">
      <div style="margin-bottom:30px;">
        <span><el-link target="blank" href="https://github.com/Gqiangqiang/freedate/blob/master/README.md#getdate%E5%AD%97%E7%AC%A6%E5%AF%B9%E7%85%A7%E8%A1%A8comparison-table" type="primary" icon="el-icon-document" style="font-size:20px;">文档GitHub</el-link></span>
        <span style="position:relative;left:40px;"><el-link target="blank" href="https://gitee.com/gqiangqiang/freedate/blob/master/README.md#getdate%E5%AD%97%E7%AC%A6%E5%AF%B9%E7%85%A7%E8%A1%A8comparison-table" type="primary" icon="el-icon-document" style="font-size:20px">文档Gitee</el-link></span></div>
      <div class="methods method1">
        <div class="label">方法一：getDate()<span class="top-time">{{DateStr}}</span></div>
        <div class="methods-items">
          <span>参数一：</span>
            <el-input placeholder="格式化字符串" v-model="m1.str1"></el-input>
          <span>参数二：</span>
            <el-date-picker v-model="m1.str2" type="datetime" placeholder="选择日期时间"></el-date-picker>
          <el-button type="primary" @click="getDate">GetDate</el-button>
            <span class="redata">返回值：</span><el-input v-model="rem1"></el-input>
        </div>
      </div>
      <div class="methods method2">
        <div class="label">方法二：getTS()<span class="top-time">{{NowTS}}</span></div>
        <div class="methods-items">
          <span>参数一(数字)：</span>
            <el-input placeholder="输入数字改变获取的时间" v-model="m2.str1"></el-input>
          <span>参数一(日期)：</span>
            <el-date-picker v-model="m2.str2" type="datetime" placeholder="请选择日期时间"></el-date-picker>
          <el-button type="primary" @click="getTS">GetTS</el-button>
            <span class="redata">返回值：</span><el-input v-model="rem2"></el-input>
        </div>
      </div>
      <div class="methods method3">
        <div class="label">方法三：tsToDate()<span class="top-time">{{NowDate}}</span></div>
        <div class="methods-items">
          <span>参数一：</span>
            <el-input  placeholder="需要转换的timestamp字符串" v-model="m3.str1"></el-input>
          <span>参数二：</span>
            <el-input v-model="m3.str2"  placeholder="格式化字符串"></el-input>
          <el-button type="primary" @click="tsToDate">TSToDate</el-button>
            <span class="redata">返回值：</span><el-input v-model="rem3"></el-input>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import fd from 'freedate'
export default {
  name: 'App',
  data(){
    return {
      m1:{str1: "", str2: null},
      m2:{str1: "", str2: null},
      m3:{str1: "", str2: ""},
      rem1:"",
      rem2:"",
      rem3:"",
      NowTS:"",
      NowDate:"",
      DateStr:""
    }
  },
  mounted(){
    setInterval(() => {
      this.NowTS = fd.getTS();
      this.NowDate = fd.tsToDate(fd.getTS());
      this.DateStr = fd.getDate();
    }, 1000);
  },
  components: {
  },
  methods:{
    getDate(){
      let m1 = this.m1.str1;
      let m2 = this.m1.str2;
      if(m2 === null){
        this.rem1 = fd.getDate(m1);
      } else {
        this.rem1 = fd.getDate(m1,m2);
      }
    },
    getTS(){
      let m1 = this.m2.str1;
      let m2 = this.m2.str2;
      if(m1 !== ""){
        let reg = /^[-+]?([1-9]\d*|0)$/;
        if(!reg.test(m1)){
          this.$message.error("请输入只有一个符号的纯数字");
        } else {
          this.rem2 = fd.getTS(parseInt(m1));
        }
      } else {
        this.rem2 = fd.getTS();
      }
      if(m2 !== null){
        this.rem2 = fd.getTS(m2);
      }
    },
    tsToDate(){
      let m1 = this.m3.str1;
      let m2 = this.m3.str2;
      if(m1 !== ""){
        let reg = /^([1-9]\d*|0)$/;
        if(!reg.test(m1)){
          this.$message.error("请输入纯数字");
        } else {
          if(m2 !== ""){
            this.rem3 = fd.tsToDate(parseInt(m1),m2);
          } else {
            this.rem3 = fd.tsToDate(parseInt(m1)).toString();
          }
        }
      } else {
        this.$message.error("第一个参数不能为空");
      }
    }
  },
  watch:{
    "m2.str1":function(){
      if(this.m2.str1 !== ""){
        this.m2.str2 = null;
      }
    },
    "m2.str2":function(){
      if(this.m2.str2 !== null){
        this.m2.str1 = "";
      }
    }
  }
}
</script>

<style scoped>
#app{
  margin: 0;
  padding: 0;
}
.app-top{
  text-align: center;
  font-size: 50px;
}
